.Dashboard__SecondPart {
  margin: 40px 10px;
  width: 1100px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 20px;
  max-width: 100vw;
}

.Dashboard__SecondPart__columnone {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Dashboard__SecondPart__columnTwo {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
}

.DashBoard__columns {
  display: flex;
}
